<template>
   <div class='updatecoach' @click='hideSelect'>
       <div class='top'>
           <span class='fundInfo' @click='goback()'>学员管理</span>
           <img src='../../assets/images/icon_nav_arrow.png'/>
           <span class='branch'>学员信息</span>
       </div>
       <div class='Info'>
         <div class='Idleft'>
              <div class='personId'>
                   <span>ID：</span>
                   <span>{{form.personId}}</span>
              </div>
           </div>
           <div class='IdRight'>
             <div class='edit' @click='editInfo()' v-if='!edit'><img src='../../assets/images/btn_edit.svg'/><span>编辑</span></div>
             <div v-if='edit' class='cancel' @click='cancel()'>取消</div>
             <div v-if='edit' class='sumbit' @click="submit('ruleform')">完成</div>
           </div> 
       </div>
       <div class='personInfo'>
           <div class='personleft' v-if='!edit'>
              <div class='personImg'>
                   <img :src='form.extraImage' class='coach' v-if='form.extraImage'>
                   <img src='../../assets/images/btn_user_photo.png' class='coach' v-if='!form.extraImage'/>
                   <div>
                    <span>{{form.extraName}}</span>
                    <img src='../../assets/images/icon_boy.png' class='sex_img' v-if='form.personSex === 0'/>
                    <img src='../../assets/images/icon_girl.png' class='sex_img' v-if='form.personSex === 1'/>
                   </div>
              </div>
           </div>
           <div class='personright' v-if='!edit'>
              <div class='secInfo firstsec'>
                   <span class='sec_left'>生日：</span>
                   <span class='sec_right'>{{form.personBirthday | timeformer}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>手机：</span>
                   <span class='sec_right'>{{form.phoneNum | phoneFormat}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>证件类型：</span>
                   <span class='sec_right'>{{form.personalType | IdFormat}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>身份证号：</span>
                   <span class='sec_right'>{{form.cardNum}}</span>
               </div>
           </div>
           <div class='personlast' v-if='!edit'>
               <div class='secInfo firstsec'>
                   <span class='sec_left'>分配销售：</span>
                   <span class='sec_right'>{{form.saleName}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>预约教练：</span>
                   <span class='sec_right'>{{form.coachNames | coachList}}</span>
               </div>
               <div class='secInfo'>
                   <span class='sec_left'>备注：</span>
                   <span class='sec_right'>{{form.note}}</span>
               </div>
           </div>
           <div v-if='edit' class='editrule'>
             <el-form ref="ruleform" :model="ruleform">
                <div class='img'>
                  <el-form-item label='' class='picture'>
                     <img class="normalFace" :src="ruleform.extraImage"  @click="fileSelect()" v-if='ruleform.extraImage'>
                     <img src='../../assets/images/btn_user_photo.png' class='coach' v-if='!ruleform.extraImage' @click="fileSelect()"/>
                     <input type="file" name="fileToUpload" id="fileToUpload" @change="fileSelected($event)" v-show='isShow' accept="image/*">
                </el-form-item>
                </div>
                <div class='editForm'>
                    <div class='editLeft'>
                      <el-form-item label="姓名：">
                        <el-input v-model="ruleform.extraName"></el-input>
                      </el-form-item>
                      <el-form-item label="性别：">
                        <div class='noedit'>{{ruleform.personSex | personSex}}</div>
                      </el-form-item>
                      <el-form-item label="生日：">
                        <div class='noedit'>{{ruleform.personBirthday | timeformer}}</div>
                      </el-form-item>
                      <el-form-item label="手机：">
                        <div class='noedit'>{{ruleform.phoneNum | phoneFormat}}</div>
                      </el-form-item>
                    </div>
                    <div class='editRight'>
                      <el-form-item label="证件类型：">
                        <div class='noedit'>{{ruleform.personalType | IdFormat}}</div>
                      </el-form-item>
                      <el-form-item label="证件号：">
                        <div class='noedit'>{{ruleform.cardNum}}</div>
                      </el-form-item>
                      <el-form-item label="分配销售：">
                        <el-select v-model="ruleform.salePersonId" placeholder="请选择" clearable>
                            <el-option
                            v-for="(item, index) in salelist"
                            :key="index"
                            :label="item.name"
                            :value="item.id"
                            :class="{whiteColor: index % 2 !== 0 ? true : false}">
                            {{item.name}}
                            <img src='../../assets/images/icon_right.png'/>
                            </el-option>
                        </el-select>
                      </el-form-item>
                      <div class='coachvis'>
                        <div>预约教练:</div>
                        <div class='coachli'><li v-for="item in ruleform.coachNames" :key='item.index'>{{item}}</li></div>
                      </div>
                    </div>
                    <div class='editLast'>
                      <el-form-item label="备注：" class='textTear'>
                         <el-input type="textarea" v-model="ruleform.note"></el-input>
                      </el-form-item>
                    </div>
                </div>
             </el-form>
           </div>
       </div>
        <div class='title'>{{form.personName}} &nbsp;名下的会员卡</div>
        <Table  :tableData="tableData" :tableKey="tableKey" :tabhide="tabhide" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize"
       v-on:changeVal="changeParam"></Table>
   </div>
</template>
<script>
import {getSessionStorage, isApiResponseOk} from '../../utils/com.js'
import {downMedia, getMemberDetail, querypercard, upload, getbranchcoach, updateExtra, getAllsale} from '../../services/service.js'
import {phoneFormat} from '../../filter/index.js'
import Table from '../../components/Table'
export default {
  data () {
    return {
      form: {
        registerTime: '',
        value5: '请选择',
        count: '24',
        personId: '',
        phoneNum: '',
        coachName: '',
        saleName: '',
        cardNum: '',
        extraImage: require("../../assets/images/btn_user_photo.png"),
        extraName: '',
        personSex: '',
        salePersonId: ''
      },
      ruleform: {
        name: '',
        phoneNum: '',
        note: ''
      },
      options: [
        {label: '搏击私教课', value: '1', checked: false},
        {label: '精英私教课', value: '2', checked: false},
        {label: '主管私教课', value: '3', checked: false},
        {label: '健身冠军私教课', value: '4', checked: false},
        {label: '普通常规私教课', value: '5', checked: false}
      ],
      isShow: true,
      rules: {
        count: [
          {required: true, message: '课时量不能为空'},
          {type: 'number', message: '课时量必须为整数'}
        ],
        registerTime: [{required: true, message: '报名时间不能为空', trigger: 'change'}]
      },
      tableKey: [
        {name: '会员卡ID', value: 'code', nor: true},
        {name: '会员卡种类', value: 'name', toDetail: true},
        {name: '类型', value: 'type', nor: true},
        {name: '余额', value: 'balance', nor: true},
        {name: '会员卡状态', value: 'status', nor: true},
        {name: '创建时间', value: 'createTime', nor: true}
      ],
      tableData: [
      ],
      isPage: false,
      total: 0,
      pageSize: 5,
      tabhide: true,
      para: {pageSize: 5, currentPage: 1},
      personId: '',
      edit: false,
      isShow: false,
      personImage: require("../../assets/images/btn_user_photo.png"),
      salelist: [],
      mediaId: true
    }
  },
  created () {
    // this.form = getSessionStorage('detailInfo', {}) // 从之前的页面跳转传过来的数据
    // 获取详情数据
    this.personId = this.$route.query.person
    this.getMemberInfo(this.personId)
    this.querypercard(this.personId)
  },
  components: {
    Table
  },
  methods: {
    show () {
      this.isShow = !this.isShow
    },
    hideSelect () {
      if (this.isShow) {
        this.isShow = false
      }
    },
    // 列表选择
    selcheck (item) {
      item.checked = !item.checked
    },
    // 删除选择
    deleteSel (item) {
      if (item.checked) {
        item.checked = false
      }
    },
    // 更新学员信息
    submit (ruleForm) {
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          if (this.mediaId) {
            self.ruleform.extraImage = self.ruleform.personImage
          }
          console.log('书', self.ruleform)
          let data = {
            extraImage: self.ruleform.extraImage,
            extraName: self.ruleform.extraName,
            note: self.ruleform.note,
            personId: self.ruleform.personId,
            salePersonId: self.ruleform.salePersonId
          }
          updateExtra(data).then(function (res) {
            if (isApiResponseOk(res)) {
              self.$message({
                showClose: true,
                message: '更新成功',
                type: 'success'
              })
              self.$router.push({name: 'studentlist'})
            }
          })
        }
      })
    },
    // 返回到学员管理页面
    goback () {
      this.$router.push({name: 'studentlist'})
    },
    // 更换页数
    changeParam (data) {
      this.para.currentPage = data
      this.getcardchangeRecord(this.para)
    },
    // 获取学院详情
    async getMemberInfo (data) {
      let param = {
        personId: data
      }
      const res = await getMemberDetail (param)
      if (isApiResponseOk(res)){
        if (!res.data.extraImage || (res.data.extraImage && res.data.extraImage.indexOf('http') < 0)) {
          if (res.data.personImage && (res.data.personImage.indexOf('http') < 0)) {
            const response = await downMedia ({mediaId: res.data.personImage})
            if (response) {
              res.data.extraImage = response
            }
          } else {
            if (res.data.personImage){
              res.data.extraImage = res.data.personImage
            }
            this.mediaId = false
          }
        } else {
          this.mediaId = false
        }
        if (!res.data.extraName) {
          res.data.extraName = res.data.personName
        }
        this.form = res.data
      } else {
      }
    },
    // 编辑信息
    // edit () {
    // },
    // 获取会员卡信息
    async querypercard (data) {
      const res = await querypercard (data)
      if (isApiResponseOk(res)) {
        this.tableData = res.data
        console.log('会员信息', res)
      }
    },
    async fileSelected (obj) {
      let formData = new FormData()
      let file = document.getElementById('fileToUpload').files[0]
      if (!file) return
      formData.append('image', file)
      const res = await upload(formData)
      if (isApiResponseOk(res)) {
        this.ruleform.extraImage = res.data
        this.$message({
          showClose: true,
          message: '恭喜您,上传成功',
          type: 'success'
        })
        this.mediaId = false
      } else {
        this.$message({
          message: '上传失败，请重新上传',
          type: 'warning'
        })
      }
    },
    fileSelect() {
      document.getElementById('fileToUpload').click(this)
    },
    // 获取所有的教练
    async getallcoach () {
      const res = await getAllsale()
      if (isApiResponseOk(res)) {
        this.salelist = res.data
        console.log('获取到的所有的教练', res)
      }
    },
    editInfo () {
      this.edit = true
      this.ruleform = this.form
      console.log('大苏打实打实', this.form, this.ruleform)
      this.getallcoach()
    },
    cancel () {
      this.edit = false
      this.ruleform = {}
      this.getMemberInfo(this.personId)
    }
  }
}
</script>
<style lang="sass" scoped>
.Info
    display: flex;
    height: .6rem;
    line-height: .6rem;
    background: #fff;
    border-bottom: 1px solid #EAECEF;
    align-items: center;
    .Idleft
      flex: 1;
      margin-left: .3rem;
    .IdRight
      margin-right: .2rem;
      display: flex;
      line-height: .36rem;
      .edit
        border: 1px solid #E9ECEF;
        border-radius: 4px;
        width: 1rem;
        height: .36rem;
        line-height: .36rem;
        text-align: center;
        color: #20C6BA;
        font-size: .14rem;
        cursor: pointer;
        img
          width: .14rem;
          margin-right: .1rem;
      .cancel
        border: 1px solid #E9ECEF;
        border-radius: 4px;
        width: 1rem;
        height: .36rem;
        line-height: .36rem;
        text-align: center;
        font-size: .16rem;
        color: #8597a1;
        margin-right: .12rem;
        cursor: pointer;
      .sumbit
        border: 1px solid #E9ECEF;
        border-radius: 4px;
        width: 1rem;
        font-size: .16rem;
        color: #FFFFFF;
        height: .36rem;
        line-height: .36rem;
        text-align: center;
        background-image: linear-gradient(138deg, #00DEAF 0%, #00DDB0 19%, #00B1BF 100%);
        cursor: pointer;
.personlast
  flex: .8;
  .firstsec
    margin-top: 0rem;
.title
    font-size: 18px;
    color: #8197A3;
    margin-top: .4rem;
.coachvis
    font-size: .12rem;
    color: #8597A1;
    display: flex;
    justify-items: center;
    align-items: baseline;
    .coachli
      flex: 1;
      margin-left: .3rem;
      font-size: .14rem;
      li
        list-style: none;
        display: inline-flex;
        margin-right: .2rem;
        color: #263238;
        line-height: .25rem;
.noedit
    font-size: .14rem;
    color: #263238;
    text-align: right;
    margin-right: .2rem;
.personright
  .secInfo .sec_right
    margin-left: .3rem;
.personlast
  .secInfo .sec_right
    font-size: .1rem;
    color: #263238;
    font-weight: bold;
    margin-left: .3rem;
</style>



